	module.exports = {"data":{"markdownRemark":{"html":"<h2>Dimension Reduction</h2>\n<p>Dealing with a lot of dimensions can be painful for machine learning algorithms. High dimensionality will increase the computational complexity, increase the risk of overfitting (as your algorithm has more degrees of freedom) and the sparsity of the data will grow. Hence, dimensionality reduction will project the data in a space with less dimension to limit these phenomena.</p>\n<h2>Why is dimensionality reduction useful?</h2>\n<ul>\n<li>\n<p>Projection into two dimensions is often used to facilitate the visualization of high dimensional data sets.</p>\n</li>\n<li>\n<p>When the dimensions can be given a meaningful interpretation, projection along that dimension can be used to explain certain behaviors.</p>\n</li>\n<li>\n<p>In the supervised learning case, dimensionality reduction can be used to reduce the dimension of the features, potentially leading to better performance for the learning algorithm.</p>\n</li>\n</ul>\n<h2>Dimensionality Reduction Techniques</h2>\n<ul>\n<li>Linear Discriminant Analysis <a href=\"http://scikit-learn.org/stable/modules/lda_qda.html\">LDA</a></li>\n<li>Principal Components Analysis <a href=\"http://setosa.io/ev/principal-component-analysis/\">PCA</a></li>\n<li>Kernel PCA</li>\n<li>Graph-based kernel PCA</li>\n<li>t-Distributed Stochastic Neighbor Embedding <a href=\"https://lvdmaaten.github.io/tsne/\">t-SNE</a></li>\n<li><a href=\"https://medium.com/towards-data-science/reducing-dimensionality-from-dimensionality-reduction-techniques-f658aec24dfe\">Auto Encoders</a></li>\n<li>Generalized discriminant analysis (GDA)</li>\n</ul>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://plot.ly/ipython-notebooks/principal-component-analysis/#introduction\">A step by step tutorial to Principal Component Analysis</a></li>\n<li><a href=\"https://medium.com/towards-data-science/reducing-dimensionality-from-dimensionality-reduction-techniques-f658aec24dfe\">Dimensionality Reduction Techniques</a></li>\n<li><a href=\"https://blog.treasuredata.com/blog/2016/03/25/dimensionality-reduction-techniques-where-to-begin\">Dimensionality Reduction Techniques: Where to Begin</a></li>\n</ul>","fields":{"slug":"/machine-learning/dimension-reduction/"},"frontmatter":{"title":"Dimension Reduction"}}},"pathContext":{"slug":"/machine-learning/dimension-reduction/"}}

